import { parse_axios_error } from "universal-common-scripts/functions/requests"
import { message } from "antd"
import axios from "axios"
import mixpanel from "mixpanel-browser"

/*
 * WHICH SERVER?
 * Yes, "x-rapidapi-key" is exposed, however if it is used, then captcha_response is required,
 * and the request will be required to come from nlp.domains, nlp.studio, or nlpthesaurus.com.
 * This secret key is ONLY allowed to consume APIs which we produce and secure in this manner.
 */
const DEVELOPMENT = process.env.GATSBY_ACTIVE_ENV === "development"
const apiHeaders = function (name) {
  if (DEVELOPMENT) {
    return { "content-type": "application/json" }
  } else if (name === "thesaurus") {
    return {
      "content-type": "application/json",
      "x-rapidapi-host": "nlp-studio3.p.rapidapi.com",
      "x-rapidapi-key": "024726ac3bmsh58ecf30df1dcaa9p187c38jsnae3868b21908"
      // "useQueryString": true
    }
  } else {
    return {
      "content-type": "application/json",
      "x-rapidapi-host": "domain-name-tools.p.rapidapi.com",
      "x-rapidapi-key": "024726ac3bmsh58ecf30df1dcaa9p187c38jsnae3868b21908"
      // "useQueryString": true
    }
  }
}
const apiHost = function (name) {
  if (DEVELOPMENT) {
    return "http://" + process.env.PUBLIC_API_HOST
  } else if (name === "thesaurus") {
    return "https://nlp-studio3.p.rapidapi.com"
  } else {
    return "https://domain-name-tools.p.rapidapi.com"
  }
}

/*
 * API METHOD: AVAILABILITY
 */
export function v1_domains_availability(params = {}) {
  return new Promise((resolve) => {
    if (params.domains && params.domains.length) {
      axios({
        method: "post",
        url: apiHost() + "/v1/domains_availability",
        data: params,
        headers: apiHeaders()
      })
        .then((results) => {
          /*
           * which data
           * expecting server response to have data key
           * but Axios puts its response into data key also
           */
          if (results.data && "data" in results.data) {
            resolve(results.data.data) // correct
          } else {
            console.warn('server response did not have "data" key')
            resolve(results.data)
          }
        })
        .catch((err) => {
          message.error(parse_axios_error(err), 10)
        })
        .then(() => {
          /*
           * done loading
           */
          window.doneLoading("data")
        })
    } else {
      resolve()
    }
  })
}

/*
 * API METHOD: SUGGESTIONS
 */
export function v1_domain_suggestions(params = {}) {
  if (!params || !params.str) {
    return
  }
  return new Promise((resolve) => {
    /*
     * start loading
     */
    window.isLoading("data")
    let postParams = {
      str: params.str,
      tld: params.tlds_use && params.tlds_use[0] ? params.tlds_use[0] : "com",
      site_id: params.site_id
    }
    let timeStart = Date.now()
    axios({
      method: "post",
      url: apiHost() + "/v1/domains_suggestions",
      params: postParams,
      data: { ...params },
      headers: apiHeaders()
    })
      .then((results) => {
        let data = {}
        /*
         * which data
         * expecting server response to have data key
         * but Axios puts its response into data key also
         */
        if (results.data && "data" in results.data) {
          data = results.data.data // correct
        } else if (results.data) {
          console.warn('server response did not have "data" key')
          data = results.data
        }
        /*
         * track success
         */
        try {
          let req_str = postParams.str + "." + postParams.tld
          let res_time = ((Date.now() - timeStart) / 1000).toFixed(2)
          let res_suggestions = {}
          if (data.domains) {
            for (let type in data.domains) {
              res_suggestions[type] = data.domains[type] ? data.domains[type].length : typeof data.domains[type]
            }
          }

          if (data && data.user_id) {
            mixpanel.identify(data.user_id)
          }
          mixpanel.track("/v1/domain_suggestions", {
            status: "success",
            input: req_str,
            time: res_time,
            data: res_suggestions
          })

          gtag("event", "domains_suggestions", {
            event_category: "success",
            event_label: `"${req_str}" ${res_time} ${JSON.stringify(res_suggestions)}`
          })
          console.warn(
            "EVENT",
            "domains_suggestions",
            "success",
            `"${req_str}" ${res_time} ${JSON.stringify(res_suggestions)}`
          )
        } catch (e) {
          console.error(e)

          if (data && data.user_id) {
            mixpanel.identify(data.user_id)
          }
          mixpanel.track("/v1/domain_suggestions", { status: "error", message: "parse error" })
        }
        /*
         * output data
         */
        resolve(data)
      })
      .catch((err) => {
        message.error(parse_axios_error(err), 10)
        /*
         * track error
         */
        let req_str = postParams.str + "." + postParams.tld
        let res_time = ((Date.now() - timeStart) / 1000).toFixed(2)

        if (err && err.user_id) {
          mixpanel.identify(err.user_id)
        }
        mixpanel.track("/v1/domain_suggestions", {
          status: "error",
          input: req_str,
          time: res_time,
          message: "bad response " + err.toString()
        })

        if (typeof gtag === "function") {
          gtag("event", "domains_suggestions", {
            event_category: "error",
            event_label: `"${req_str}" ${err.toString()}`
          })
          console.error("GTAG EVENT", "domains_suggestions", "error", `"${req_str}" ${res_time} ${err.toString()}`)
        } else {
          console.error("GTAG NOT INSTALLED ?", `trying to track domain_suggestions response ${err.toString()}`)
        }
      })
      .then(() => {
        /*
         * done loading
         */
        window.doneLoading("data")
      })
  })
}

/*
 * API METHOD: THESAURUS
 */
export function v1_word_thesaurus(params) {
  if (!params || !params.str) {
    return
  }
  return new Promise((resolve) => {
    /*
     * start loading
     */
    window.isLoading("data")

    axios({
      method: "get",
      url: apiHost("thesaurus") + "/v1/word/" + params.str,
      params: params,
      headers: apiHeaders("thesaurus")
    })
      .then((results) => {
        /*
         * which data
         * expecting server response to have data key
         * but Axios puts its response into data key also
         */
        if (results.data && "data" in results.data) {
          resolve(results.data.data) // correct
        } else {
          console.warn('server response did not have "data" key')
          resolve(results.data)
        }
      })
      .catch((err) => {
        message.error(parse_axios_error(err), 10)
      })
      .then(() => {
        /*
         * done loading
         */
        window.doneLoading("data")
      })
  })
}
