import { querystring_from_object, object_from_querystring } from "universal-common-scripts/functions/urls"
import { navigate } from "gatsby"

export function RX__focusSelectTld() {
  return {
    type: "RX__focusSelectTld"
  }
}

export function RX__toggle_poss() {
  return (dispatch) => {
    dispatch({
      type: "RX__toggle_poss"
    })
  }
}

export function RX__meta_title({ str = "", tld = "" }) {
  // console.log("RX__meta_title", { str, tld })
  return (dispatch, getState) => {
    let { input } = getState()

    /*
     * always show str if in Redux
     */
    if (!str && input.input_str) {
      str = input.input_str
    }

    /*
     * set title
     */
    let title = str.replace(/ /g, "") + (tld ? "." + tld : "")

    /*
     * set url
     */
    // /{page_name}?str={str}&tld={tld}
    let obj = {}
    if (str) {
      obj.str = str
    }
    if (tld) {
      obj.tld = tld
    }
    let search = querystring_from_object(obj)
    let href = window.location.pathname + search
    if (search && href !== window.location.href) {
      navigate(href)
    }

    dispatch({
      type: "RX__meta_title",
      data: title
    })
  }
}

export function RX__search_title(title) {
  return {
    type: "RX__search_title",
    data: title
  }
}

export function RX__toast(intent, message) {
  // IF BOTH ARGUMENTS PROVIDED:
  if (intent && message) {
    // remap intent
    switch (intent) {
      case "error":
        intent = "danger"
        break
      case "fail":
        intent = "warning"
        break
      default:
        intent = "success"
        break
    }
    // display second argument
    return {
      type: "RX__toast",
      intent: intent,
      message: message
    }
  }
  // OPTIONALLY, USE WITH ONLY ONE ARGUMENT:
  if (intent && !message) {
    // display first argument instead of second
    return {
      type: "RX__toast",
      intent: "",
      message: intent
    }
  }
}
