import React from "react"
// import { RX__toggle_poss } from "./ui"
import { v1_domain_suggestions, v1_word_thesaurus } from "src/redux/actions/api/v1"
import * as ui_actions from "src/redux/actions/ui"
import CONSOLE from "src/console"
import { object_from_querystring } from "universal-common-scripts/functions/urls"
import { message } from "antd"

/*
 *
 * INPUTS
 *
 */
export function RX__clear_inputs() {
  return (dispatch) => {
    /*
     * reset all reducer properties starting with "input_"
     */
    dispatch({
      type: "RX__clear_inputs",
      data: ""
    })
    /*
     * reset url (back to /src/pages/___ default)
     */
    dispatch(ui_actions.RX__meta_title({}))
  }
}
// set multiple at once, to prevent multiple UI re-renders
export function RX__set_inputs(inputs = {}) {
  return (dispatch) => {
    /*
     * modify inputs
     */
    // parse tld from str
    if (inputs.str) {
      let str = inputs.str
      let io = str.indexOf(".")
      if (io !== -1) {
        let tld = str.substr(io + 1)
        if (tld) {
          inputs.tld = tld
        }
        inputs.str = str.substr(0, io)
      }
    }
    /*
     * A1: Prepare URL
     */
    let url_obj = object_from_querystring(window.location.search)
    /*
     * B1: Prepare state
     */
    for (let key in inputs) {
      switch (key) {
        case "str":
          {
            // reset generated values if new string
            inputs.spellchecked = ""
            inputs.first_word = ""
            // filter
            inputs[key] = inputs[key].replace(/[^\w\d. ]+/g, "")
            // set new first word
            let i_space = inputs.str.indexOf(" ")
            if (i_space !== -1) {
              inputs.first_word = inputs[key].substring(0, i_space)
            } else {
              inputs.first_word = inputs[key]
            }
            // set URL
            url_obj.str = inputs.str
          }
          break

        case "tld":
          {
            // set URL
            url_obj.tld = inputs.tld
          }
          break
        case "spellchecked":
          {
            inputs[key] = inputs[key].replace(/[^\w\d ]+/g, "")
          }
          break
      }
    }
    /*
     * A2: set URL
     */
    dispatch(ui_actions.RX__meta_title(url_obj))
    /*
     * B2: set state
     * in reducer, properties starting with "input_"
     * pass them here as keys, omitting the prefix "input_"
     */
    dispatch({
      type: "RX__set_inputs",
      data: inputs
    })
    /*
     * special case, side-effect when setting "tld"
     */
    if (inputs.tld) {
      dispatch({
        type: "RX__tld_user",
        data: inputs.tld
      })
    }
    /*
     * trigger search
     */
    dispatch(RX__search_now())
  }
}

/*
 *
 * SEARCH: trigger
 *
 */
export function RX__search_now() {
  return (dispatch) => {
    /*
     * The search action is determined in the page which is currently in view -
     * - but when to execute the action is triggered from Search.js component.
     */
    dispatch({
      type: "RX__search_now"
    })
  }
}

/*
 *
 * SEARCH: thesaurus
 *
 */
let RX__search_thesaurus__in_progress = false
export function RX__search_thesaurus(options = {}) {
  CONSOLE.info("RX__search_thesaurus()", options)
  return (dispatch, getState) => {
    /*
     * arguments
     */
    let { input, output } = getState()
    if (!input.input_str) {
      //console.log("ignore search_thesaurus() because !input.input_str")
      return
    }

    /*
     * prevent redundant calls
     */
    if (output.chunks[input.input_str] && !options.force) {
      //console.log("ignore search_thesaurus() because output.chunks[input.input_str]")
      return
    }

    /*
     * prevent simultaneous calls
     */
    if (RX__search_thesaurus__in_progress) {
      //console.log("ignore search_thesaurus() because input.in_progress")
      return
    }
    RX__search_thesaurus__in_progress = true
    setTimeout(function () {
      RX__search_thesaurus__in_progress = false
    }, 1000)

    /*
     * send request
     */
    let params = {
      str: input.input_str,
      recaptcha2_token: input.input_captcha2,
      recaptcha3_token: input.input_captcha3
    }
    v1_word_thesaurus(params).then(function (row = {}) {
      setTimeout(function () {
        RX__search_thesaurus__in_progress = false
      }, 1000)

      /*
       * got response
       */
      dispatch({
        type: "RX__add_chunk",
        data: row || {}
      })
    })
  }
}

/*
 *
 * SEARCH: suggestions
 *
 */
export function RX__search_suggestions(options) {
  return (dispatch, getState) => {
    /*
     * save options
     */
    if (options) {
      dispatch({
        type: "RX__set_suggestions_options",
        data: options
      })
    } else {
      options = {}
    }

    /*
     * arguments
     */
    let { input } = getState()
    if (!input.input_str) {
      //console.log("ignore search_suggestions() because !input.input_str")
      return
    }

    /*
     * data
     */
    let params = {
      ...input.suggestions_options,
      ...options,
      str: input.input_str,
      tlds_use: [input.input_tld],
      tlds_ignore: []
    }
    // console.log("RX__search_suggestions()", params)

    /*
     * prevent redundant calls
     */
    let params_stringified = JSON.stringify(params) + Date.now() / 1000 // limit
    if (params_stringified === input.params_stringified) {
      //console.log("ignore search_suggestions() because params_stringified === input.params_stringified")
      return
    }
    dispatch({
      type: "RX__params_stringified",
      data: params_stringified
    })

    /*
     * prevent simultaneous calls
     * IMPORTANT: this valudation (return) must be the last validation before the API request,
     * because if "in_progress" flag gets set, but the API does not execute, app will stall.
     */
    if (input.in_progress) {
      //console.log("ignore search_suggestions() because input.in_progress")
      return
    }
    dispatch({
      type: "RX__in_progress",
      data: true
    })
    setTimeout(function () {
      dispatch({
        type: "RX__in_progress",
        data: false
      })
    }, 1000)

    /*
     * clear data: set after get back responses
     */
    dispatch({
      type: "RX__input_spellchecked",
      data: ""
    })
    dispatch({
      type: "RX__domain_suggestions",
      results: {
        domains: {},
        wip_domains: [],
        phrases: [],
        word_hacks: [],
        phrase_lists: []
      }
    })

    /*
     * add captcha token
     * (AFTER comparing params_stringified changes, because captcha param should ALWAYS be different)
     */
    params.recaptcha2_token = input.input_captcha2
    params.recaptcha3_token = input.input_captcha3

    /*
     * send request
     */
    v1_domain_suggestions(params)
      .then(async function (results = {}) {
        // end request
        dispatch({
          type: "RX__in_progress",
          data: false
        })

        /*
         * got response
         */
        // tld - always set, even if already specified,
        // because this one is parsed from the query phrase.
        // parsed value overrides value specified by dropdown
        // dispatch(RX__set_inputs({ tld: results.tld }))

        // phrase - spellchecked str
        dispatch({
          type: "RX__set_inputs",
          data: { spellchecked: results.string }
        })

        // dictionary of DB rows per word
        dispatch({
          type: "RX__chunks",
          data: results.chunks_rows
        })

        // the main attraction
        dispatch({
          type: "RX__domain_suggestions",
          results: {
            domains: results.domains,
            wip_domains: results.wip_domains,
            phrases: results.phrases,
            phrase_lists: results.phrase_lists,
            word_hacks: results.word_hacks
          }
        })

        // suggested tlds
        dispatch({
          type: "RX__tlds_checked",
          data: results.tlds
        })

        // suggested tlds which did not fit, because there are too many
        dispatch({
          type: "RX__tlds_extra",
          data: results.tlds_extra
        })

        // captcha expires time
        dispatch({
          type: "RX__auth_expires",
          data: results.auth_expires
        })

        // if string changed, but not from microsoft spellcheck...
        if (results.string_possibly_corrupted) {
          // then possibly some words got corrupted
          message.warning(
            `We're still improving parsing of phrases. For now, for best results, please add a space between each word.`,
            10
          )
        } else if (localStorage.getItem("initial_wip_message") !== "7") {
          message.warning(
            <span>
              <b className="color-attention">Thanks for trying this out!</b> This is still a work in progress. We're
              working hard every day to make it better. Please do send us your feedback.
            </span>,
            10
          )
          localStorage.setItem("initial_wip_message", "7")
        }
      })
      .catch(function () {
        // end request
        dispatch({
          type: "RX__in_progress",
          data: false
        })
      })
  }
}
